import Vue from "vue";

import VueRouter from "vue-router";

Vue.use(VueRouter)



export default new VueRouter({

    routes: [

        {
            redirect: '/whiteList',
            path: '',
            name: '',
        },
        {
            path: '/whiteList',
            name: 'whiteList',
            component: () =>
                import ('../views/whiteList/index'),
            meta: {
                title: '学平险'
            }
        },
        {
            path: '/scanIdCard',
            name: 'scanIdCard',

            component: () =>
                import ('../views/whiteList/scanIdCard'),
            meta: {
                title: '证件上传'
            }
        },
        {
            path: '/insuranceInformation',
            name: 'insuranceInformation',
            component: () =>
                import ('../views/insuranceInformation/index'),
            meta: {
                title: '学平险详情'
            }
        },
        {
            path: '/errPages',
            name: 'errPages',
            component: () =>
                import ('../views/errPages/errPages'),
            meta: {
                title: '错误页面'
            }
        },
        {
            path: '/healthInform',
            name: 'healthInform',
            component: () =>
                import ('../views/insuranceInformation/components/healthInform'),
            meta: {
                title: '健康告知'
            }
        },
        {
            path: '/insuredObject',
            name: 'insuredObject',
            component: () =>
                import ('../views/insuranceInformation/components/insuredObject'),
            meta: {
                title: '为谁投保'
            }
        },
        {
            path: '/informationEntry',
            name: 'informationEntry',
            component: () =>
                import ('../views/informationEntry'),
            meta: {
                title: '信息录入'
            }
        },
        {
            path: '/insuranceApplication',
            name: 'insuranceApplication',
            component: () =>
                import ('../views/insuranceApplication'),
            meta: {
                title: '信息录入'
            }
        },
        {
            path: '/signature',
            name: 'signature',
            component: () =>
                import ('../views/insuranceApplication/signature'),
            meta: {
                title: '签名'
            }
        },
        {
            path: '/insuranceSynthetic',
            name: 'insuranceSynthetic',
            component: () =>
                import ('../views/insuranceSynthetic/index'),
            meta: {
                title: '投保单合成'
            }
        },
        {
            path: '/productTerms',
            name: 'productTerms',
            component: () =>
                import ('../views/productTerms/index'),
            meta: {
                title: '产品条款'
            }
        },
        {
            path: '/cardResults',
            name: 'cardResults',
            component: () =>
                import ('../views/cardResults/index'),
            meta: {
                title: '结果页面'
            }
        },
        {
            path: '/planPage',
            name: 'planPage',
            component: () =>
                import ('../views/planPage/index'),
            meta: {
                title: '方案页面'
            }
        },


    ]
})